<template>
  <div id="change-password-component">
    <el-card
      class="card"
      :body-style="{
        padding: '20px 50px'
      }"
    >
      <div class="title-container">
        <p>Invitation</p>
      </div>
      <div class="main">
        <div class="item">
          <p>Email</p>
          <el-input type="email" v-model="form.email"></el-input>
        </div>
        <div class="item">
          <p>Brand</p>
          <el-autocomplete
            class="input"
            v-model="form.brand"
            :fetch-suggestions="onQuerySearchBrand"
            placeholder="Brand..."
          ></el-autocomplete>
        </div>
        <div class="item">
          <p>Company Type</p>
          <el-select v-model="form.type" placeholder="Type...">
            <el-option value="buyer" label="Buyer">
              Buyer
            </el-option>
            <el-option value="garment_factory" label="Garment Factory">
              Garment Factory
            </el-option>
          </el-select>
        </div>
        <div class="item">
          <p>Business unit</p>
          <el-autocomplete
            class="input"
            v-model="form.businessUnit"
            :fetch-suggestions="onQuerySearchBusinessUnit"
            placeholder="Business Unit..."
          ></el-autocomplete>
        </div>
        <div class="item">
          <p>Text</p>
          <el-input type="textarea" rows="8" v-model="form.text"></el-input>
        </div>
      </div>
      <div class="footer">
        <el-button type="success" @click="onSendClick">Send</el-button>
      </div>
    </el-card>
  </div>
</template>

<script lang="ts">
import { defineComponent, reactive, ref, onMounted } from "vue";
import { postCreateInvitation } from "@/apis/auth";
import { ElNotification } from "element-plus";
import { getCompanies } from "@/apis/company";
import { CompanyCardResponse } from "@/typings/company";
import { getMemberBusinessUnits } from "@/apis/member";
import { createInviteHtml } from "@/utils/createInviteHtml";
import { emailRule } from "@/utils";

const initText = () => {
  return (
    "Dear ,\n" +
    "We are delighted to invite you to join Weaverbird.\n" +
    "Follow the attached link to activate your account.\n" +
    "We look forward to welcoming you as a new member."
  );
};

const Component = defineComponent({
  name: "ChangePassword",
  setup() {
    const form = reactive({
      email: "",
      brand: null,
      type: "buyer",
      businessUnit: "",
      text: initText()
    });
    const companyList = ref<string[]>([]);
    const businessUnitList = ref<string[]>([]);
    const isLock = ref(false);

    const init = async () => {
      const { data: companies } = await getCompanies({
        category: "invitation"
      });
      const { data: businessUnit } = await getMemberBusinessUnits();
      companyList.value = companies.map((it: CompanyCardResponse) => it.name);
      businessUnitList.value = businessUnit;
    };

    onMounted(init);

    const createFilter = (queryString: string) => {
      return (option: string) => {
        return option.toLowerCase().indexOf(queryString.toLowerCase()) === 0;
      };
    };

    const onQuerySearchBrand = async (queryString: string, cb: Function) => {
      const results = queryString
        ? companyList.value.filter(createFilter(queryString))
        : companyList.value;
      cb(results.map((text: string) => ({ value: text })));
    };

    const onQuerySearchBusinessUnit = async (
      queryString: string,
      cb: Function
    ) => {
      const results = queryString
        ? businessUnitList.value.filter(createFilter(queryString))
        : businessUnitList.value;
      cb(results.map((text: string) => ({ value: text })));
    };

    const onSendClick = async () => {
      if (isLock.value) return;
      if (!form.email || !emailRule.test(form.email)) {
        ElNotification({
          title: "Error",
          type: "error",
          message: `Please enter a valid email address.`
        });
        return;
      }
      if (!form.brand) {
        ElNotification({
          title: "Error",
          type: "error",
          message: `Please select the brand. `
        });
        return;
      }
      const params = {
        email: form.email,
        company: form.brand,
        business_unit: form.businessUnit,
        trial_type: form.type,
        html_message: createInviteHtml(form.brand!, form.text)
      };
      try {
        isLock.value = true;
        const { data } = await postCreateInvitation(params);
        ElNotification({
          title: "Success",
          type: "success",
          message: `Invitation successfully sent to ${data.email}`
        });
      } catch (e) {
        ElNotification({
          title: "Error",
          type: "error",
          message: `Can't send Invitation.`
        });
      } finally {
        form.email = "";
        form.brand = null;
        form.businessUnit = "";
        form.text = initText();
        isLock.value = false;
      }
    };

    return {
      form,

      onQuerySearchBusinessUnit,
      onQuerySearchBrand,
      onSendClick
    };
  }
});

export default Component;
</script>

